<template>
  <div>
    <el-form :inline="true">
      <el-form-item>
        <el-input
            v-model="searchForm.fieldName"
            placeholder="请输入接口名称"
            clearable
        >
        </el-input>
      </el-form-item>

      <el-form-item>
        <el-button @click="getFieldList">搜索</el-button>
      </el-form-item>

      <el-form-item>
        <!--				<el-button type="primary" @click="dialogVisible = true" v-if="hasAuth('sys:field:save')">新增</el-button>-->
        <el-button type="primary" @click="dialogVisible = true" >新增</el-button>
      </el-form-item>
      <el-form-item>
        <el-popconfirm title="这是确定批量删除吗？" @confirm="delHandle(null)">
          <!--					<el-button type="danger" slot="reference" :disabled="delBtlStatu" v-if="hasAuth('sys:user:delete')">批量删除</el-button>-->
          <el-button type="danger" slot="reference" :disabled="delBtlStatu" >批量删除</el-button>
        </el-popconfirm>
      </el-form-item>
    </el-form>

    <el-table
        :data="tableData"
        style="width: 100%;margin-bottom: 20px;"
        row-key="id"
        border
        stripe
        default-expand-all
        :tree-props="{children: 'children', hasChildren: 'hasChildren'}">

      <el-table-column
              type="selection"
              width="55">
      </el-table-column>

      <el-table-column
              prop="id"
              label="ID"
              width="50">
      </el-table-column>

      <el-table-column
          prop="name"
          label="名称"
          width="120">
      </el-table-column>

      <el-table-column
          prop="httpType"
          label="请求类型"
          width="100">
      </el-table-column>

      <el-table-column
          prop="opType"
          label="操作类型"
          width="100">
      </el-table-column>

      <el-table-column
          prop="model"
          label="操作对象"
          width="100">
      </el-table-column>

      <el-table-column
          prop="data"
          label="接口参数"
          width="200">
      </el-table-column>

      <el-table-column
          prop="filter"
          label="过滤条件"
          width="200">
      </el-table-column>

      <el-table-column
              prop="other"
              label="查询设置"
              width="200">
      </el-table-column>

      <el-table-column
          prop="createUser"
          label="创建者"
          width="100">
      </el-table-column>

      <el-table-column
          prop="accessUser"
          label="授权用户">
      </el-table-column>

      <el-table-column
              prop="status"
              label="状态">
        <template slot-scope="scope">
          <el-tag size="small" v-if="scope.row.status === 1" type="success">正常使用</el-tag>
          <el-tag size="small" v-else-if="scope.row.status === 0" type="danger">禁用</el-tag>
        </template>
      </el-table-column>

      <el-table-column
          prop="icon"
          label="操作"
          width="260px">

        <template slot-scope="scope">
          <el-button type="text" @click="editHandle(scope.row.id)">编辑</el-button>
          <el-divider direction="vertical"></el-divider>

          <template>
            <el-popconfirm title="这是一段内容确定删除吗？" @confirm="delHandle(scope.row.id)">
              <el-button type="text" slot="reference">删除</el-button>
            </el-popconfirm>
          </template>

        </template>
      </el-table-column>

    </el-table>

    <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        layout="total, sizes, prev, pager, next, jumper"
        :page-sizes="[10, 20, 50, 100]"
        :current-page="current"
        :page-size="size"
        :total="total">
    </el-pagination>

    <!--新增对话框-->
    <el-dialog
        title="新增接口"
        :visible.sync="dialogVisible"
        width="600px"
        :before-close="handleClose">

      <el-form :model="editForm" :rules="editFormRules" ref="editForm">
        <el-form-item label="名称"  prop="name" label-width="100px">
          <el-input v-model="editForm.name" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="remark"  prop="remark" label-width="95px">
          <el-input v-model="editForm.remark" autocomplete="off"></el-input>
        </el-form-item>

        <!--        <el-form-item label="请求类型" :rules="editFormRules" label-width="100px" >-->
        <!--          <el-select v-model="editForm.httpType" placeholder="请选择请求类型" size="small">-->
        <!--            <el-option label="get" value="get"></el-option>-->
        <!--            <el-option label="post" value="post"></el-option>-->
        <!--          </el-select>-->
        <!--        </el-form-item>-->

        <el-form-item label="请求类型" :rules="editFormRules" prop="httpType" label-width="105px">
          <el-radio-group v-model="editForm.httpType">
            <el-radio :label="0">get</el-radio>
            <el-radio :label="1">post</el-radio>
          </el-radio-group>
        </el-form-item>

        <el-form-item label="opType" :rules="editFormRules" label-width="100px" >
          <el-select v-model="editForm.opType" placeholder="请选择操作类型" size="small">
            <el-option label="put" value="put"></el-option>
            <el-option label="post" value="post"></el-option>
            <el-option label="delete" value="delete"></el-option>
            <el-option label="get" value="get"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="model"  prop="model" :rules="editFormRules" label-width="95px">
          <el-input v-model="editForm.model" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="modelNames"  prop="modelNames" :rules="editFormRules" label-width="95px">
          <el-input v-model="editForm.modelNames" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="data"  prop="data" :rules="editFormRules" label-width="95px">
          <el-input v-model="editForm.data" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="filter"  prop="filter" :rules="editFormRules" label-width="95px">
          <el-input v-model="editForm.filter" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="状态"  prop="status" :rules="editFormRules" label-width="105px">
          <el-radio-group v-model="editForm.status">
            <el-radio :label="0">正在使用</el-radio>
            <el-radio :label="1">禁用</el-radio>
          </el-radio-group>
        </el-form-item>

        <el-form-item label="createUser" prop="createUser" :rules="editFormRules" label-width="105px" >
          <el-input-number v-model="editForm.createUser" @change="handleChange" :min="0" :max="10000" ></el-input-number>
        </el-form-item>

        <el-form-item label="允许访问" prop="accessUser" :rules="editFormRules" label-width="105px" >
          <el-input-number v-model="editForm.accessUser" @change="handleChange" :min="0" :max="10000" ></el-input-number>
        </el-form-item>

      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="resetForm('editForm')">取 消</el-button>
        <el-button type="primary" @click="submitForm('editForm')">确 定</el-button>
      </div>
    </el-dialog>

  </div>

</template>

<script>
export default {
  name: "Menu",
  data() {
    return {

      total: 0,
      size: 10,
      current: 1,

      searchForm: {},
      delBtlStatu: true,
      dialogVisible: false,

      editForm: {

      },
      editFormRules: {
        name: [
          {required: true, message: '请输入名称', trigger: 'blur'}
        ],
        httpType: [
          {required: true, message: '请选择请求类型', trigger: 'blur'}
        ],
        opType: [
          {required: true, message: '请选择操作类型', trigger: 'blur'}
        ],
        model: [
          {required: true, message: '请输入model', trigger: 'blur'}
        ],
        modelNames: [
          {required: true, message: '请输入modelNames', trigger: 'blur'}
        ],
        data: [
          {required: true, message: '请输入data', trigger: 'blur'}
        ],
        filter: [
          {required: true, message: '请输入filter', trigger: 'blur'}
        ],
        status: [
          {required: true, message: '请选择状态', trigger: 'blur'}
        ],
        createUser: [
          {required: true, message: '请输入接口创建用户ID', trigger: 'blur'}
        ],
        accessUser: [
          {required: true, message: '请输入accessUser', trigger: 'blur'}
        ],
      },
      tableData: []
    }
  },
  created() {

  },
  methods: {
    getFieldList() {
      this.$axios.get("/sys/user/list", {
        params: {
          username: this.searchForm.username,
          current: this.current,
          size: this.size
        }
      }).then(res => {
        this.tableData = res.data.data.records
        this.size = res.data.data.size
        this.current = res.data.data.current
        this.total = res.data.data.total
      })
    },

    resetForm(formName) {
      this.$refs[formName].resetFields();
      this.dialogVisible = false
      this.editForm = {}
    },

    handleClose() {
      this.resetForm('editForm')
    },

    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.size = val
      this.getUserList()
    },

    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.current = val
      this.getUserList()
    },

  }
}
</script>

<style scoped>
.el-pagination {
  float: right;
  margin-top: 22px;
}
</style>